"use client";
import React, { useState } from "react";
import {
  useMutation,
  useQuery,
  useQueryClient,
} from "@tanstack/react-query";
import { CloudCog } from "lucide-react";

// ‚úÖ UI Components
const Input = ({ placeholder, value, onChange, className }: any) => (
  <input
    type="text"
    placeholder={placeholder}
    value={value}
    onChange={onChange}
    className={`w-full p-3 rounded-lg border border-gray-300 bg-gray-800 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 transition duration-200 ${className}`}
  />
);

const Button = ({ children, onClick, disabled, className }: any) => (
  <button
    onClick={onClick}
    disabled={disabled}
    className={`w-full sm:w-auto px-6 py-3 rounded-lg bg-gradient-to-r from-blue-600 to-purple-600 text-white font-semibold hover:from-blue-700 hover:to-purple-700 disabled:opacity-50 disabled:cursor-not-allowed transition duration-200 ${className}`}
  >
    {children}
  </button>
);

const Card = ({ children, className }: any) => (
  <div className={`bg-gray-800 rounded-lg shadow-lg p-6 ${className}`}>
    {children}
  </div>
);

const CardContent = ({ children, className }: any) => (
  <div className={`space-y-4 ${className}`}>{children}</div>
);

// ‚úÖ API Calls
const createScrapeJob = async (url: string) => {
  const res = await fetch("/api/scrape/add", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ url, scraper_type: "beautifulsoup", tags: [] }),
  });
  if (!res.ok) throw new Error("Failed to start scraping job");
  return res.json();
};

const fetchScrapeStatus = async (job_id: string) => {
  
    console.log(job_id,'iddd')
  const res = await fetch(`/api/scrape/status/${job_id}`);
    console.log(res,'res')
  if (!res.ok) throw new Error("Failed to get job status");
  return res.json();
};

// ‚úÖ Main Page Component
export default function Home() {
  const [url, setUrl] = useState("");
  const [jobId, setJobId] = useState<string | null>(null);

  const queryClient = useQueryClient();

  // üöÄ Start Scraping
  const { mutate: startScraping, isPending: isSubmitting } = useMutation({
    mutationFn: () => createScrapeJob(url),
    onSuccess: (data) => {
      setJobId(data.data.job_id); // ‚úÖ Correct access path
    },
    onError: (err: any) => {
      alert(err?.message || "Something went wrong");
    },
  });

  // üîÅ Polling for Status
  const {
    data: jobResult,
    isLoading: isPolling,
    isError,
    error,
  } = useQuery({
    queryKey: ["scrape-status", jobId],
    queryFn: () => fetchScrapeStatus(jobId!),
    enabled: !!jobId,
    refetchInterval: (data) =>
      data?.data?.status === "completed" || data?.data?.status === "error" ? false : 2000,
  });

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-900 to-gray-800 py-10 px-4 sm:px-8 md:px-16 flex items-center justify-center">
      <div className="max-w-4xl w-full mx-auto space-y-8">
        {/* üî∑ Header */}
        <div className="text-center">
          <h1 className="text-4xl sm:text-5xl font-bold text-white tracking-tight">üß† AI Web Scraper</h1>
          <p className="mt-2 text-gray-300 text-lg sm:text-xl">
            Paste any article or website URL to extract content and generate AI-powered insights like summaries, sentiment, and SEO tags.
          </p>
        </div>

        {/* üìù Input */}
        <div className="flex flex-col sm:flex-row gap-4">
          <Input
            placeholder="https://example.com/article"
            value={url}
            onChange={(e: any) => setUrl(e.target.value)}
            className="flex-grow"
          />
          <Button onClick={() => startScraping()} disabled={isSubmitting || !url}>
            {isSubmitting ? (
              <span className="flex items-center gap-2">
                <svg className="animate-spin h-5 w-5 text-white" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8v8z" />
                </svg>
                Scraping...
              </span>
            ) : (
              "Start Scraping"
            )}
          </Button>
        </div>

        {/* üìä Results */}
        {jobResult && (
          <Card className="mt-8">
            <CardContent>
              <h2 className="text-2xl font-semibold text-white">Results</h2>

              {jobResult.status === "error" ? (
                <p className="text-red-400">
                  ‚ùå {jobResult?.result?.error || "Something went wrong."}
                </p>
              ) : jobResult.status !== "completed" ? (
                <p className="text-gray-400">‚è≥ Scraping in progress... Please wait.</p>
              ) : (
                <div className="space-y-4 text-gray-200">
                  <div>
                    <strong className="text-blue-400">Headline:</strong>{" "}
                    {jobResult?.result?.headline || "-"}
                  </div>
                  <div>
                    <strong className="text-blue-400">TL;DR:</strong>{" "}
                    {jobResult?.result?.tldr || "-"}
                  </div>
                  <div>
                    <strong className="text-blue-400">Summary:</strong>
                    <p className="text-sm text-gray-300 whitespace-pre-wrap">
                      {jobResult?.result?.summary || "-"}
                    </p>
                  </div>
                  <div>
                    <strong className="text-blue-400">Topics:</strong>{" "}
                    {jobResult?.result?.topics?.join(", ") || "-"}
                  </div>
                  <div>
                    <strong className="text-blue-400">Sentiment:</strong>{" "}
                    {jobResult?.result?.sentiment?.label} (
                    {jobResult?.result?.sentiment?.score || "-"})
                  </div>
                  <div>
                    <strong className="text-blue-400">SEO Tags:</strong>{" "}
                    {jobResult?.result?.seo_tags?.join(", ") || "-"}
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        )}
      </div>
    </main>
  );
}
